<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process267"></a>

<p><br></p>
<p><span class="rvts17">怜悯就是力量（</span><span class="rvts50">Mercy is Power</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:390,Y:899</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：温蒂格</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">杀掉温蒂格或帮助温蒂格摆脱神王的束缚</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1476.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">该支线任务需要首先来到兵营监狱中，尝试说服一处具有地下楼梯的兵营看守，进入地下层。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1477.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">这里关押着温蒂格，接着还需要说服她周围的狱卒，才能单独靠近温蒂格。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1478.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts21">温蒂格被关进了一处具有源力限制的牢笼中，作为臭名昭著的源力罪犯，她得到如今的下场是罪有应得。然而，温蒂格是由于受到神王的束缚（神王盟约），才落到如此下场。</span>
<br><span class="rvts21">记得使用她牢笼旁边的闸刀，把她释放出来。（如果使用闸刀的角色是在欢乐堡铁匠帮助下去除项圈的那位，则会出现</span><span class="rvts20"></span><span class="rvts21">这个闸刀与欢乐堡铁匠处工作原理相似</span><span class="rvts20"></span><span class="rvts21">的对话，这样将不会消耗使用者的源力打开它）</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1479.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">尝试说服温蒂格，她会选择继续出逃，但这次不同的是，她会告诉你她要找到林德</span><span class="rvts20"></span><span class="rvts21">凯姆藏宝室中的</span><span class="rvts20"></span><span class="rvts21">破誓者</span><span class="rvts20"></span><span class="rvts21">，用来解除她与神王的盟约。</span>
<br><span class="rvts21">如果此时我们手上有破誓者，便可以交给她，她会非常感谢我们，奖励神圣武器和一本独特的召唤系技能书</span><span class="rvts20"></span><span class="rvts21">魅惑虚空醒者</span><span class="rvts20"></span><span class="rvts21">（这本召唤书是独有的，整个游戏只有这一本）。该支线将以好的结局关闭。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1480.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">之后我们依然会在林德</span><span class="rvts20"></span><span class="rvts21">凯姆藏宝室找到她的尸体和灵魂。温蒂格通过使用</span><span class="rvts20"></span><span class="rvts21">破誓者</span><span class="rvts20"></span><span class="rvts21">最终得到了解放（但是从对话中感觉似乎不是那样），可以从她身上搜出我们给她的那把已经耗尽耐久的破誓者。</span>
<br><span class="rvts21">这把破损的武器依然可以交给复仇女神号上的阿玛蒂亚用来完成</span><span class="rvts20"></span><span class="rvts21">自由之钥</span><span class="rvts20"></span><span class="rvts21">支线或交给</span><span class="rvts20"></span><span class="rvts21">理事会</span><span class="rvts20"></span><span class="rvts21">支线中的蜥蜴公主（前提是队伍中需要有猩红王子）。</span>
<br><span class="rvts21">完成</span><span class="rvts20"></span><span class="rvts21">自由之钥</span><span class="rvts20"></span><span class="rvts21">会使得阿玛蒂亚在最终章的</span><span class="rvts20">BOSS</span><span class="rvts21">战中，为我们祝福，减少技能的源力消耗。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



